import './myfooter.less'

export default {
  data () {
    return {}
  },
  created () {
    this.fix()
  },
  methods: {
    // 修改键盘弹出样式
    fix () {
      var wHeight = window.innerHeight   // 获取初始可视窗口高度

      window.onresize = function () {
        var hh = window.innerHeight  // 当前可视窗口高度
        if (wHeight > hh) {           // 可以作为虚拟键盘弹出事件
          document.querySelector('.foot-warp').style.display = 'none'
        } else {         // 可以作为虚拟键盘关闭事件
          document.querySelector('.foot-warp').style.display = 'block'
        }
        wHeight = hh
      }
    }
  },
  components: {}
}



